#root{
    height: inherit;
}

html,body {
    font-family: "Roboto", sans-serif;
    /* overflow-x: hidden; */
    background: rgb(35,40,64);
    height: 100%;
}
*::before,
*::after,
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* brand styling */
.brand_styling{
    font-family: 'Dosis', sans-serif;
}

/* dropdown menu header */
.menu_user .dropdown-menu{
    left:-100% !important;
}

.purchase_placeholder_token::-webkit-input-placeholder {
    color: rgb(177, 179, 184);
}

.purchase_placeholder_token:-ms-input-placeholder {
    color: rgb(177, 179, 184);
}

.purchase_placeholder_token::placeholder {
    color: rgb(177, 179, 184);
}
.purchase_placeholder_token::-webkit-outer-spin-button,
.purchase_placeholder_token::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
.purchase_placeholder_token {
    -moz-appearance: textfield;
}

.href_style_text{
    cursor: pointer;
}
.href_style_text:hover{
    text-decoration: underline;
}

.react-es-searchbox{
    max-width: 60%;
    margin: auto;
    background: transparent;
    z-index: 2;
}

.react-es-searchbox > input{
    width: 100%;
    border-radius: 20px;
    padding: 7px 20px;
    border: none;
    box-shadow: 0px 0px 10px 3px black;
    outline: none;
    z-index: 2;
}

.text_primary{
    color:lightcyan;
}

.nav-item.active{
    background: transparent;
}

.nav-item:hover{
    border:none;
    color:lightcoral;
}

.nav-tabs .nav-link.active{
    border:none;
    color:lightgreen;
    border-bottom: 1px solid lightgreen;
    background: transparent;
}

.react-confirm-alert-overlay{
    z-index: 1000000 !important;
}

.interaction_bar_icon:hover{
    color:lightcoral;
}

.list_video_container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px,auto));
    width: 95%;
    margin: auto;
    margin-top: 20px;
    padding:15px;
}
.img-container > img{
    width: 340px;
    height: 190px;
    object-fit: cover;
}
.profile-wrapper{
    position: relative;
    width: 80px;
}
.profile-wrapper > img{
    box-shadow:  1px 2px 8px black ;
    border-radius: 50px;
    border: 1px silver solid;
    max-width: 50px;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
}
.list_video_item {
    margin-top: 10px;
    display: -webkit-flex;
    display: flex;
    position: relative;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 340px;
    margin: auto;
    border-radius: 20px;
}

.list_video_description {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    padding:10px;
}

.list_video_status {
    position: absolute;
    top: 3%;
    left: 3%;
    padding: 5px;
    border-radius: 5px;
    background: red;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 17px;
    font-weight: bold;
    color: white;
}
.list_video_viewers {
    position: absolute;
    left: 3%;
    padding: 3px;
    border-radius: 5px;
    background: #333;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-size: 17px;
    color: white;
    opacity: 0.7;
}

.list_video_tags {
    background: #414155;
    margin: 5px;
    border-radius: 8px;
}
